<template>
  <div class="alloc-menu">
     <!-- 页头（返回） -->
    <el-page-header
      content="分配菜单"
      @back="goBack"></el-page-header>
    <!-- 菜单分配树 -->
    <el-card>
      <el-tree
        ref="menu-tree"
        node-key="id"
        :data="menus"
        :props="defaultProps"
        :default-checked-keys="checkedKeys"
        default-expand-all
        show-checkbox
      ></el-tree>
      <div style="margin: 20px;">
        <el-button type="primary" @click="onSave">保存</el-button>
        <el-button @click="onReset">清空</el-button>
      </div>
    </el-card>
  </div>
</template>

<script>
import {
  getMenuNodeList,
  getRoleMenus,
  allocateRoleMenus
} from '@/services/menu'

export default {
  name: 'AllocMenu',
  props: {
    roleId: {
      type: [Number, String],
      required: true
    }
  },
  data () {
    return {
      menus: [],
      defaultProps: {
        label: 'name',
        children: 'subMenuList'
      },
      checkedKeys: []
    }
  },
  created () {
    this.loadMenus()
    this.loadRoleMenus()
  },
  methods: {
    async loadMenus () {
      const { data } = await getMenuNodeList()
      if (data.code === '000000') {
        this.menus = data.data
      }
    },
    async loadRoleMenus () {
      const { data } = await getRoleMenus(this.roleId)
      if (data.code === '000000') {
        this.getCheckedKeys(data.data)
      }
    },
    async onSave () {
      const { data } = await allocateRoleMenus({
        roleId: this.roleId,
        menuIdList: this.$refs['menu-tree'].getCheckedKeys()
      })
      if (data.code === '000000') {
        this.$message.success('分配菜单成功')
        this.$router.push({
          name: 'role'
        })
      }
    },
    onReset () {
      this.$refs['menu-tree'].setCheckedKeys([])
    },
    getCheckedKeys (menus) {
      menus.forEach(menu => {
        if (!menu.selected) return
        if (menu.subMenuList) {
          return this.getCheckedKeys(menu.subMenuList)
        }
        this.checkedKeys = [...this.checkedKeys, menu.id]
      })
    },
    goBack () {
      this.$router.back()
    }
  }
}
</script>

<style lang="scss" scoped>
.alloc-menu {
  .el-page-header {
    margin-bottom: 20px;
  }
}
</style>
